<template>
  <section></section>
</template>

<script>
export default {
  data () {
    return {
      test: [2, 1, 3, 4, 12, 3, 1, 5, 7]
    }
  },
  methods: {
    /**
     * 
     * 
     * 
     */
    fast (arr) {
      if (arr.length <= 1) {
        return arr
      }
      let leftArr = []
      let rightArr = []
      let q = arr[0]
      for (let i = 1; i < arr.length; i++) {
        if (q > arr[i]) {
          leftArr.push(arr[i])
        } else {
          rightArr.push(arr[i])
        }
      }
      return [].concat(this.fast(leftArr), [q], this.fast(rightArr))
    },
    bubble (arr) {
      for (let i = 0; i < arr.length - 1; i++) {
        for (let j = i; j < arr.length; j++) {
          if (arr[i] > arr[j]) {
            let m = arr[i]
            arr[i] = arr[j]
            arr[j] = m
          }
        }
      }
      return arr
    },
    /**
     * 
     * 
     * 
     */
    insert (arr) { // [3, 2, 1]
      for (let i = 1, l = arr.length; i < l; i++) {
        let temp = arr[i] // 1
        let j = i - 1 // 1
        while (j >= 0 && arr[j] > temp) {
          arr[j + 1] = arr[j] // 2
          j-- // 0
        }
        console.log(j)
        arr[j + 1] = temp // 1
      }
      return arr
    },
    insertTwo (arr) {
      let p = [arr.shift()]
      for (let i = 0, l = arr.length; i < l; i++) {
        let j = i
        while (j >= 0 && p[j] > arr[i]) {
          p[j + 1] = p[j]
          j--
        }
        p[j + 1] = arr[i]
      }
      return p
    },
    selection (arr) {
      let l = arr.length
      for (let i = 0; i < l - 1; i++) {
        let minIndex = i
        for (let j = i + 1; j < l; j++) {
          if (arr[j] < arr[minIndex]) {
            minIndex = j
          }
        }
        let temp = arr[i]
        arr[i] = arr[minIndex]
        arr[minIndex] = temp
      }
      return arr
    },
    init () {
      function Animal (name) {
        this.name = name || 'animal' // 
        this.type = 'animal-type'
        // 
        this.sleep = function () {
          console.log(this.name + '')
        }
      }

      // 
      Animal.prototype.eat = function (food) {
        console.log(this.name + '' + food)
      }

      /**
       * 
       *
       * 
       * 
       * 
       * 
       *    new Animal 
       *    
       *    
       *    
       * @constructor
       */
      function Cat () {
        this.name = 'catt'
        // this.sleeps = function () {
        //   console.log(this.name + '')
        // }
      }

      Cat.prototype = new Animal()
      Cat.prototype.name = 'cat'
      // let cat = new Cat()
      // cat.sleeps()
      // console.log(cat.type)
      // console.log(cat instanceof Animal)
      // console.log(cat instanceof Cat)
      /**
       * 
       *    1
       *    
       *    
       * 
       *    
       *    
       *    
       * @param name
       * @constructor
       */
      function Dog (name) {
        Animal.call(this)
        this.name = name || 'dog'
      }
      let dog = new Dog()
      console.log(dog)

      // 
      function Tom (name) {
        let instance = new Animal()
        instance.name = name || 'Tom'
        return instance
      }
      let tom = new Tom()
      console.log(tom)

      // 
      function Jury (name) {
        this.name = name || 'jury'
        let animal = new Animal()
        for (let i in animal) {
          Jury.prototype[i] = animal[i]
        }
        // Jury.prototype.name = name || 'jury'
      }
      let jury = new Jury()
      console.log(jury)
      /**
       * 
       */
      function Fish (name) {
        Animal.call(this, name)
        this.name = name || 'fish'
      }
      Fish.prototype = new Animal()
      Fish.prototype.constructor = Fish
      let fish = new Fish('')
      console.log(fish)
      /**
       * 
       */
      function Mao (name) {
        Animal.call(this, name)
        this.name = name
      }
      function extend (child, parent) {
        let F = function () {}
        F.prototype = parent.prototype
        child.prototype = new F()
        child.prototype.constructor = child
      }
      extend(Mao, Animal)
      let mao = new Mao('')
      console.log(mao.name)
    },
    swap (a, b) {
      b = b - a
      a = a + b
      b = a - b
      return [a, b]
    },
    fibo (n) {
      let arr = []
      let i = 0
      while (i < n) {
        if (i <= 1) {
          arr.push(i)
        } else {
          arr.push(arr[i - 1] + arr[i - 2])
        }
        i++
      }
      return arr
    }
  },
  mounted () {
    let t = [2, 1]
    console.log(this.insert(t))
  }
}
</script>

